body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* header{
  background-color: black;
  color:white;
  padding: 20px;
  text-align: center;
}  */

.header{
  padding:20px 70px;
  background-color: black;
  color:white;
  display: flex;
  justify-content: space-between;
}
.cart-icon .counter{
  height:18px;
  width: 18px;
  background-color: blue;
  border-radius: 50%;
  right:7px;
  text-align:center;
  position: relative;
  /* display: flex; */
  top:-7px
}
.cart-icon {
  display: flex;

}
.quantity{
  display:flex;
  align-self: flex-end;
  border-radius: 3px;
  border:1px solid grey;
}
.icon{
     font-size: 22px;
   
}
img{
  height:150px;
}
.card-wrapper{
    display: inline-flex;
    /* margin: 40px;
    width: 320px; */
    box-shadow: 0px 0px 7px black;
    border-radius: 5px;
}
.image-wrapper{
  width:180px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-right:1px solid rgb(201,201,201);
}

.Content{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  gap:10px;
  padding: 10px;
}
.price{
  margin-bottom: auto;
}

.Content button{
  background-color: black;
  color: white;
  border-radius: 3px;
}
.quantity div{
  height: 25px;
  width: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity > div:first-child, .quantity> button{

    background-color: blue;
    color: white;
    border-radius: none ;
}

.quantity > button:disabled{
  opacity: 0.7;
}
main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap:50px;
}


 .footer h1{
  background-color: black;
  color:white;
  text-align: right;
 
}

.footer{
  bottom:0;
  position: fixed;
  width:100%;
}
